.login-container {
    // border: 1px solid black;
    min-width: 50vh;
    min-height: 50vh;
    border: 1px solid #512474;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 1.5rem 2rem 1.5rem;
    -webkit-box-shadow: 0px 0px 11px -1px rgba(0, 0, 0, 0.45);
    box-shadow: 0px 0px 11px -1px rgba(0, 0, 0, 0.45);
}



@import url('https://fonts.googleapis.com/css?family=Open+Sans:600|Work+Sans:400,500,600,700&display=swap');

.login-body {
    background: linear-gradient(90deg, rgba(34, 193, 195, 1) 0%, rgb(0, 98, 255) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
}

#loginform {
    max-width: 500px;
    min-width: 300px;
    max-height: 700px;
    width: 30%;
    height: 60%;
    margin: 100px auto;
    background-color: #FFFFFF;
    border-radius: 25px;
}

#headerTitle {
    text-align: center;
    font-family: 'open sans', sans-serif;
    padding: 2rem 0;
    margin: 0;
    font-size: 2rem;
}

.row {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    max-width: 100%;

}

.row input {
    width: 80%;
    box-sizing: border-box;
    border: none;
    font-size: 1.3rem;
    padding-left: 1.5rem;
    padding-top: 0.5rem;
    box-shadow: inset 0px -3px 0px 0px rgba(187, 187, 187, 0.2);
    transition: box-shadow 0.2s ease-in;
}

.row input:focus {
    box-shadow: inset 0px -3px 0px 0px rgba(34, 193, 195, 0.7);
    outline: none;
}

.row input::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.25s ease-out;
}

.row input:hover::-webkit-input-placeholder,
.row input:focus::-webkit-input-placeholder {
    opacity: 0;
}

.row label {
    align-self: start;
    padding-left: 3.5rem;
    padding-bottom: 0.5rem;
    color: rgba(187, 187, 187, 0.9);
}

.row button {
    border-radius: 25px;
    width: 80%;
    height: 40px;
    font-size: 1.3rem;
    color: white;
    font-weight: 700;
    background: rgb(34, 193, 195);
    background: linear-gradient(90deg, rgba(34, 193, 195, 1) 0%, rgb(0, 98, 255) 100%);
    border: 0px;
    cursor: pointer;
    transition: opacity 0.25s ease-out;
}

.row button:hover {
    opacity: 0.8;
}

#button {
    padding-bottom: 1.5rem;
}

.form-control  {
    width: 80%;
    box-sizing: border-box;
    border: none;
    font-size: 1.3rem;
    padding-left: 1.5rem;
    color: #7a777b;
    padding-top: 0.5rem;
    box-shadow: inset 0px -3px 0px 0px rgba(187, 187, 187, 0.2);
    transition: box-shadow 0.2s ease-in;
}

.form-control :focus {
    box-shadow: inset 0px -3px 0px 0px rgba(34, 193, 195, 0.7);
    outline: none;
}

.form-control ::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.25s ease-out;
}

.form-control :hover::-webkit-input-placeholder,
.form-control :focus::-webkit-input-placeholder {
    opacity: 0;
}
